import { UserTypeCode } from '../enums/UserTypeCode';
import { UserStatusCode } from '../enums/UserStatusCode';

/*  
*/
export class UsersServiceFindRequest {
 
    // Filter by account id (get anlu Account Users with access to the accountId) 
    public accountId: string;
 
    // Filter by name or partial name or id 
    public search: string;
 
    // Filter by user type(s) (empty or UNDEFINED for no filter) 
    public type: UserTypeCode[];
 
    // Filter by user status(es) (empty or UNDEFINED for no filter) 
    public status: UserStatusCode[];
 
    // Sort by field and direction: (e.g. time+ = sort by time asc, id- = sort by id desc) 
    public sort: string;
 
    // Page number (for pagination) 
    public page: number;
 
    // Number of items per page (for pagination) 
    public pageSize: number;
 
    constructor(accountId?: string, search?: string, type?: UserTypeCode[], status?: UserStatusCode[], sort?: string, page?: number, pageSize?: number) { 
        this.accountId = accountId;
        this.search = search;
        this.type = type;
        this.status = status;
        this.sort = sort;
        this.page = page;
        this.pageSize = pageSize;
    }

}


