import { Injectable, Inject } from '@angular/core';
import { RestUtil } from '../../utils/rest-util';
import { CoreConfig } from '../../config';

import { Booking } from '../entities/Booking';


/**
 * Services for managing kayak resources - for account administrator only
 * @RequestHeader X-API-KEY The key to identify the application (portal)
 * @RequestHeader X-ACCESS-TOKEN The token to identify the logged-in user 
 */
@Injectable()
export class UserBookingsService {

  // URL to web api
  private baseUrl = '/api/user/bookings';

  /**
   * Class constructor
   */
  constructor(@Inject('config') private config: CoreConfig, private rest: RestUtil) {
    this.baseUrl = this.config.api + this.baseUrl;
  }


  /**
   * Create new booking
   * @Return: EntityResponse<Booking>
   */
  create(body?: Booking) {
    return this.rest.post(`${this.baseUrl}`, typeof body === 'object' ? JSON.stringify(body) : body);
  }

  /**
   * Update booking
   * @Return: EntityResponse<Booking>
   */
  update(body?: Booking) {
    return this.rest.put(`${this.baseUrl}`, typeof body === 'object' ? JSON.stringify(body) : body);
  }

  /**
   * Delete booking
   * @Return: ActionResponse
   */
  delete(id?: string) {
    return this.rest.delete(`${this.baseUrl}`);
  }

  /**
   * Get single booking by id
   * @Return: EntityResponse<Booking>
   */
  get(id?: string) {
    return this.rest.get(`${this.baseUrl}/${id}`);
  }

  /**
   * Find bookings by filters
   * @Return: QueryResponse<Booking>
   */
  find(userId?: string, resourceId?: string, sort?: string, page?: number, pageSize?: number) {
    const params = new Array();		
    if (userId != null) { params.push(`userId=${userId}`); }
    if (resourceId != null) { params.push(`resourceId=${resourceId}`); }
    if (sort != null) { params.push(`sort=${sort}`); }
    if (page != null) { params.push(`page=${page}`); }
    if (pageSize != null) { params.push(`pageSize=${pageSize}`); }

		return this.rest.get(`${this.baseUrl}`, ...params);
  }

}
