import { Injectable, Inject } from '@angular/core';
import { RestUtil } from '../../utils/rest-util';
import { CoreConfig } from '../../config';

import { Account } from '../entities/Account';
import { AccountTypeCode } from '../enums/AccountTypeCode';
import { AccountStatusCode } from '../enums/AccountStatusCode';


/**
 * List of account related actions for system administrator only
 * @RequestHeader X-API-KEY The key to identify the application (console)
 * @RequestHeader X-ACCESS-TOKEN The token to identify the logged-in user 
 */
@Injectable()
export class SysAccountsService {

  // URL to web api
  private baseUrl = '/api/sys/accounts';

  /**
   * Class constructor
   */
  constructor(@Inject('config') private config: CoreConfig, private rest: RestUtil) {
    this.baseUrl = this.config.api + this.baseUrl;
  }


  /**
   * Create new account
   * @Return: EntityResponse<Account>
   */
  create(body?: Account) {
    return this.rest.post(`${this.baseUrl}`, typeof body === 'object' ? JSON.stringify(body) : body);
  }

  /**
   * Update existing account in the system
   * @Return: EntityResponse<Account>
   */
  update(body?: Account) {
    return this.rest.put(`${this.baseUrl}`, typeof body === 'object' ? JSON.stringify(body) : body);
  }

  /**
   * Delete account from the system
   * The account is moved to DELETED mode and will be deleted after 90 days
   * Only account marked as SUSPENDED can be deleted
   * @Return: ActionResponse
   */
  delete(id?: string) {
    return this.rest.delete(`${this.baseUrl}`);
  }

  /**
   * Delete account immediately without account status restrictions
   * @Return: ActionResponse
   */
  purge(id?: string) {
    return this.rest.delete(`${this.baseUrl}/purge/${id}`);
  }

  /**
   * Reset account - remove all operational data older than the retention time in days (events, status, log ...) but leave configuration data
   * @Return: ActionResponse
   */
  reset(id?: string, days?: number) {
    return this.rest.delete(`${this.baseUrl}/reset/${id}/days/${days}`);
  }

  /**
   * Get single account by id
   * @Return: EntityResponse<Account>
   */
  get(id?: string) {
    return this.rest.get(`${this.baseUrl}/${id}`);
  }

  /**
   * Find list of accounts and filter
   * @Return: QueryResponse<Account>
   */
  find(search?: string, type?: AccountTypeCode[], status?: AccountStatusCode[], sort?: string, page?: number, pageSize?: number) {
    const params = new Array();		
    if (search != null) { params.push(`search=${search}`); }
    if (type != null) { params.push(`type=${type}`); }
    if (status != null) { params.push(`status=${status}`); }
    if (sort != null) { params.push(`sort=${sort}`); }
    if (page != null) { params.push(`page=${page}`); }
    if (pageSize != null) { params.push(`pageSize=${pageSize}`); }

		return this.rest.get(`${this.baseUrl}`, ...params);
  }

}
