import { Injectable, Inject } from '@angular/core';
import { RestUtil } from '../../utils/rest-util';
import { CoreConfig } from '../../config';

import { UserInvitation } from '../common/UserInvitation';
import { User } from '../entities/User';
import { UserTypeCode } from '../enums/UserTypeCode';
import { UserStatusCode } from '../enums/UserStatusCode';


/**
 * List of all user related actions for account administrator only 
 */
@Injectable()
export class UsersService {

  // URL to web api
  private baseUrl = '/api/admin/users';

  /**
   * Class constructor
   */
  constructor(@Inject('config') private config: CoreConfig, private rest: RestUtil) {
    this.baseUrl = this.config.api + this.baseUrl;
  }


  /**
   * Send invitation to a new user for the current account
   * @Return: ActionResponse
   */
  invite(body?: UserInvitation) {
    return this.rest.post(`${this.baseUrl}/invite`, typeof body === 'object' ? JSON.stringify(body) : body);
  }

  /**
   * Resend invitation to an existing user for the current account
   * @Return: ActionResponse
   */
  reInvite(id?: string) {
    return this.rest.post(`${this.baseUrl}/re-invite/${id}`, null);
  }

  /**
   * Update user
   * @Return: EntityResponse<User>
   */
  update(id?: string, body?: User) {
    return this.rest.put(`${this.baseUrl}/${id}`, typeof body === 'object' ? JSON.stringify(body) : body);
  }

  /**
   * Change user name
   * @Return: EntityResponse<User>
   */
  changeName(id?: string, body?: string) {
    return this.rest.put(`${this.baseUrl}/${id}/name`, typeof body === 'object' ? JSON.stringify(body) : body);
  }

  /**
   * Change user mobile
   * @Return: EntityResponse<User>
   */
  changeMobile(id?: string, body?: string) {
    return this.rest.put(`${this.baseUrl}/${id}/mobile`, typeof body === 'object' ? JSON.stringify(body) : body);
  }

  /**
   * Change user type
   * @Return: EntityResponse<User>
   */
  changeType(id?: string, type?: UserTypeCode) {
    return this.rest.put(`${this.baseUrl}/${id}/type/${type}`, null);
  }

  /**
   * Delete user from the system
   * The user will be removed from the account, if no accounts associated with the user, it will be deleted
   * @Return: ActionResponse
   */
  delete(id?: string) {
    return this.rest.delete(`${this.baseUrl}/${id}`);
  }

  /**
   * Get single user by id
   * @Return: EntityResponse<User>
   */
  get(id?: string) {
    return this.rest.get(`${this.baseUrl}/${id}`);
  }

  /**
   * Get single user by email
   * @Return: EntityResponse<User>
   */
  getByEmail(email?: string) {
    return this.rest.get(`${this.baseUrl}/byEmail/${email}`);
  }

  /**
   * Find list of users and filter the list
   * System user will see all users, Account system will see all users of the account, registered user will get an error.
   * @Return: QueryResponse<User>
   */
  find(accountId?: string, search?: string, type?: UserTypeCode[], status?: UserStatusCode[], sort?: string, page?: number, pageSize?: number) {
    const params = new Array();		
    if (accountId != null) { params.push(`accountId=${accountId}`); }
    if (search != null) { params.push(`search=${search}`); }
    if (type != null) { params.push(`type=${type}`); }
    if (status != null) { params.push(`status=${status}`); }
    if (sort != null) { params.push(`sort=${sort}`); }
    if (page != null) { params.push(`page=${page}`); }
    if (pageSize != null) { params.push(`pageSize=${pageSize}`); }

		return this.rest.get(`${this.baseUrl}`, ...params);
  }

  /**
   * Get access token for user
   * @Return: ActionResponse
   */
  getUserToken(id?: string, exp?: number) {
    return this.rest.get(`${this.baseUrl}/${id}/token/${exp}`);
  }

}
