
/* 
   Entity response message returned for read operation on a single entity 
*/
export class EntityResponse {
 
    // Error code (0 for success) 
    public code: number;
 
    // Error message 
    public error: string;
 
    constructor(code?: number, error?: string) { 
        this.code = code;
        this.error = error;
    }

}


