import { Injectable, Inject } from '@angular/core';
import { RestUtil } from '../../utils/rest-util';
import { CoreConfig } from '../../config';

import { AccountRoleCode } from '../enums/AccountRoleCode';
import { UserRegistration } from '../common/UserRegistration';
import { User } from '../entities/User';
import { UserTypeCode } from '../enums/UserTypeCode';
import { UserStatusCode } from '../enums/UserStatusCode';


/**
 * List of all user related actions for account administrator only
 * @RequestHeader X-API-KEY The key to identify the application (console)
 * @RequestHeader X-ACCESS-TOKEN The token to identify the logged-in user 
 */
@Injectable()
export class SysUsersService {

  // URL to web api
  private baseUrl = '/api/sys/users';

  /**
   * Class constructor
   */
  constructor(@Inject('config') private config: CoreConfig, private rest: RestUtil) {
    this.baseUrl = this.config.api + this.baseUrl;
  }


  /**
   * Create a new user for the current account
   * The response includes access token valid for 20 minutes. The client side should renew the token before expiration using refresh-token method
   * @Return: ActionResponse
   */
  create(body?: UserRegistration) {
    return this.rest.post(`${this.baseUrl}`, typeof body === 'object' ? JSON.stringify(body) : body);
  }

  /**
   * Update user
   * @Return: EntityResponse<User>
   */
  update(id?: string, body?: User) {
    return this.rest.put(`${this.baseUrl}/${id}`, typeof body === 'object' ? JSON.stringify(body) : body);
  }

  /**
   * Change user name
   * @Return: EntityResponse<User>
   */
  changeName(id?: string, body?: string) {
    return this.rest.put(`${this.baseUrl}/${id}/name`, typeof body === 'object' ? JSON.stringify(body) : body);
  }

  /**
   * Change user mobile
   * @Return: EntityResponse<User>
   */
  changeMobile(id?: string, body?: string) {
    return this.rest.put(`${this.baseUrl}/${id}/mobile`, typeof body === 'object' ? JSON.stringify(body) : body);
  }

  /**
   * Change user type
   * @Return: EntityResponse<User>
   */
  changeType(id?: string, type?: UserTypeCode) {
    return this.rest.put(`${this.baseUrl}/${id}/type/${type}`, null);
  }

  /**
   * Change user status
   * @Return: EntityResponse<User>
   */
  changeStatus(id?: string, status?: UserStatusCode) {
    return this.rest.put(`${this.baseUrl}/${id}/status/${status}`, null);
  }

  /**
   * Change user default account
   * @Return: EntityResponse<User>
   */
  changeDefaultAccount(id?: string, accountId?: string) {
    return this.rest.put(`${this.baseUrl}/${id}/defaultAccount/${accountId}`, null);
  }

  /**
   * Reset password for user, generate one-time temporary password
   * @Return: ActionResponse
   */
  resetPassword(id?: string) {
    return this.rest.post(`${this.baseUrl}/${id}/reset-password`, null);
  }

  /**
   * Set user roles in his accounts (override previous roles)
   * @Return: EntityResponse<User>
   */
  setRoles(id?: string, roles?: AccountRoleCode[]) {
    return this.rest.post(`${this.baseUrl}/${id}/roles/`, typeof roles === 'object' ? JSON.stringify(roles) : roles);
  }

  /**
   * Update user roles in his accounts (merge with existing roles)
   * @Return: EntityResponse<User>
   */
  mergeRoles(id?: string, roles?: AccountRoleCode[]) {
    return this.rest.put(`${this.baseUrl}/${id}/roles/`, typeof roles === 'object' ? JSON.stringify(roles) : roles);
  }

  /**
   * Delete user from the system
   * @Return: ActionResponse
   */
  delete(id?: string) {
    return this.rest.delete(`${this.baseUrl}/${id}`);
  }

  /**
   * Get single user by Id
   * @Return: EntityResponse<User>
   */
  get(id?: string) {
    return this.rest.get(`${this.baseUrl}/${id}`);
  }

  /**
   * Find list of users by filter
   * @Return: QueryResponse<User>
   */
  find(accountId?: string, search?: string, type?: UserTypeCode[], status?: UserStatusCode[], sort?: string, page?: number, pageSize?: number) {
    const params = new Array();		
    if (accountId != null) { params.push(`accountId=${accountId}`); }
    if (search != null) { params.push(`search=${search}`); }
    if (type != null) { params.push(`type=${type}`); }
    if (status != null) { params.push(`status=${status}`); }
    if (sort != null) { params.push(`sort=${sort}`); }
    if (page != null) { params.push(`page=${page}`); }
    if (pageSize != null) { params.push(`pageSize=${pageSize}`); }

		return this.rest.get(`${this.baseUrl}`, ...params);
  }

}
