/* 
   Account role code (represent role of user in the account) 
*/
export enum AccountRoleCode {
 
    // Undefined [0] 
    UNDEFINED = 0,
 
    // Account administrator can perform all operations [1] 
    ADMIN = 1,
 
    // Part of a club staff [2] 
    STAFF = 2,
 
    // Club Member [3] 
    MEMBER = 3,
 
    // Olympic team [4] 
    TEAM = 4,
 
    // Para-olympic team [5] 
    PARA = 5,
 
    // Club guest [6] 
    GUEST = 6,
 
}