
/* 
   Token request message 
*/
export class TokenRequest {
 
    // Error code (0 for success) 
    public code: number;
 
    // Error message 
    public error: string;
 
    // Account Id 
    public accountId: string;
 
    constructor(code?: number, error?: string, accountId?: string) { 
        this.code = code;
        this.error = error;
        this.accountId = accountId;
    }

}


