/* 
   Time unit interval 
*/
export enum TimeUnitCode {
 
    // Undefined [0] 
    UNDEFINED = 0,
 
    // Second interval [1] 
    SECOND = 1,
 
    // Minute interval [2] 
    MINUTE = 2,
 
    // Hour interval [3] 
    HOUR = 3,
 
    // Day interval [4] 
    DAY = 4,
 
    // Week interval [5] 
    WEEK = 5,
 
    // Month interval [6] 
    MONTH = 6,
 
}