import { BookingStatusCode } from '../enums/BookingStatusCode';

/*  
*/
export class UserPlacementFindRequest {
 
    // Filter by user who placed the booking 
    public userId: string;
 
    // Filter by resource Id 
    public resourceId: string;
 
    // From booking time [Epoch milliseconds Timestamp] 
    public from: number;
 
    // To booking time [Epoch milliseconds Timestamp] 
    public to: number;
 
    // Filter by referenced booking Id 
    public bookingId: string;
 
    // Filter by booking status 
    public status: BookingStatusCode;
 
    // Sort by field and direction: (e.g. time+ = sort by time asc, id- = sort by id desc) 
    public sort: string;
 
    // Page number (for pagination) 
    public page: number;
 
    // Number of items per page (for pagination) 
    public pageSize: number;
 
    constructor(userId?: string, resourceId?: string, from?: number, to?: number, bookingId?: string, status?: BookingStatusCode, sort?: string, page?: number, pageSize?: number) { 
        this.userId = userId;
        this.resourceId = resourceId;
        this.from = from;
        this.to = to;
        this.bookingId = bookingId;
        this.status = status;
        this.sort = sort;
        this.page = page;
        this.pageSize = pageSize;
    }

}


