/* 
   User status code 
*/
export enum UserStatusCode {
 
    // Undefined [0] 
    UNDEFINED = 0,
 
    // User is registered and pending verification [1] 
    PENDING = 1,
 
    // Active user in the system [2] 
    ACTIVE = 2,
 
    // Blocked user (only account system can unblock the user) [4] 
    BLOCKED = 3,
 
    // Suspended user (about to be deleted) [8] 
    SUSPENDED = 4,
 
}