import { AccountRoleCode } from '../enums/AccountRoleCode';

/* 
   User invitation data model - used by account admin to invite user to the account 
*/
export class UserInvitation {
 
    // User email 
    public email: string;
 
    // User role in the account 
    public role: AccountRoleCode;
 
    constructor(email?: string, role?: AccountRoleCode) { 
        this.email = email;
        this.role = role;
    }

}


