import { UserTypeCode } from '../enums/UserTypeCode';
import { UserStatusCode } from '../enums/UserStatusCode';

/*  
*/
export class UsersServiceExportRequest {
 
    // Filter by name or partial name or id 
    public search: string;
 
    // Filter by user type(s) (empty or UNDEFINED for no filter) 
    public type: UserTypeCode[];
 
    // Filter by user status(es) (empty or UNDEFINED for no filter) 
    public status: UserStatusCode[];
 
    // Sort by field and direction: (e.g. time+ = sort by time asc, id- = sort by id desc) 
    public sort: string;
 
    // Export results to a different format. Supported formats: csv | json | xml 
    public format: string;
 
    // List of fields to export (leave empty to export all fields) 
    public fields: string[];
 
    constructor(search?: string, type?: UserTypeCode[], status?: UserStatusCode[], sort?: string, format?: string, fields?: string[]) { 
        this.search = search;
        this.type = type;
        this.status = status;
        this.sort = sort;
        this.format = format;
        this.fields = fields;
    }

}


