import { WeightRange } from '../common/WeightRange';
import { BaseEntity } from '../entities/BaseEntity';
import { ResourceClassCode } from '../enums/ResourceClassCode';
import { ResourceTypeMask } from '../enums/ResourceTypeMask';
import { ResourceStatusCode } from '../enums/ResourceStatusCode';
import { UseTypeCode } from '../enums/UseTypeCode';

/* 
   Resource type 
*/
export class Resource extends BaseEntity {
 
    // Resource Identity in the club 
    public name: string;
 
    // Resource class 
    public resourceClass: ResourceClassCode;
 
    // Resource type 
    public resourceType: ResourceTypeMask;
 
    // Resource status (Available | non-available ...) 
    public status: ResourceStatusCode;
 
    // Resource used by 
    public forUseBy: UseTypeCode;
 
    // Manufacturer brand 
    public brand: string;
 
    // Minimum Maximum weight [Kg] 
    public weightRange: WeightRange;
 
    // Description 
    public description: string;
 
    // Entity Id 
    public id: string;
 
    // Entity document type (system field) 
    public _type: string;
 
    // When the entity was created [Epoch milliseconds Timestamp] 
    public createdOn: number;
 
    // When the entity was updated [Epoch milliseconds Timestamp] 
    public updatedOn: number;
 

}


