import { BaseEntity } from '../entities/BaseEntity';
import { AccountRoleCode } from '../enums/AccountRoleCode';
import { EntityTypeCode } from '../enums/EntityTypeCode';

/* 
   Audit Log entry - represents a single action done by user 
*/
export class AuditLog extends BaseEntity {
 
    // Account Id 
    public accountId: string;
 
    // User Id 
    public userId: string;
 
    // User role in the account when the action was performed 
    public userRole: AccountRoleCode;
 
    // User name when the action was performed 
    public userName: string;
 
    // Action that was performed 
    public action: string;
 
    // Changed item type 
    public itemType: EntityTypeCode;
 
    // Changed item Id 
    public itemId: string;
 
    // Item Name 
    public itemName: string;
 
    // Item value before change [Json] 
    public beforeChange: string;
 
    // Item delta after change [Json] 
    public afterChange: string;
 
    // Entity Id 
    public id: string;
 
    // Entity document type (system field) 
    public _type: string;
 
    // When the entity was created [Epoch milliseconds Timestamp] 
    public createdOn: number;
 
    // When the entity was updated [Epoch milliseconds Timestamp] 
    public updatedOn: number;
 

}


