import { BaseEntity } from '../entities/BaseEntity';

/* 
   Booking request
   A booking is a pending request to book a resource for specific time for user(s), once it is approved, it becomes a placement 
*/
export class Booking extends BaseEntity {
 
    // When to ask for placement [Epoch milliseconds Timestamp] 
    public placementOn: number;
 
    // For how log to book the resource [minutes] 
    public duration: number;
 
    // User Id who requested the booking 
    public requestedBy: string;
 
    // List of User Ids for the placement 
    public requestedFor: string[];
 
    // Booking for specific resource 
    public resourceId: string;
 
    // Entity Id 
    public id: string;
 
    // Entity document type (system field) 
    public _type: string;
 
    // When the entity was created [Epoch milliseconds Timestamp] 
    public createdOn: number;
 
    // When the entity was updated [Epoch milliseconds Timestamp] 
    public updatedOn: number;
 

}


