
/* 
   Query response message returned for find operation (with pagination) on multiple entities 
*/
export class QueryResponse {
 
    // Error code (0 for success) 
    public code: number;
 
    // Error message 
    public error: string;
 
    // Current page (Bulk) number 
    public page: number;
 
    // Size of page (items in bulk) 
    public pageSize: number;
 
    // Total number of pages 
    public pages: number;
 
    // Total number of items in the query 
    public total: number;
 
    // Query definition spec as Json string 
    public queryDef: string;
 
    // Entity type 
    public docType: string;
 
    constructor(code?: number, error?: string, page?: number, pageSize?: number, pages?: number, total?: number, queryDef?: string, docType?: string) { 
        this.code = code;
        this.error = error;
        this.page = page;
        this.pageSize = pageSize;
        this.pages = pages;
        this.total = total;
        this.queryDef = queryDef;
        this.docType = docType;
    }

}


