import { AccountTypeCode } from '../enums/AccountTypeCode';
import { AccountStatusCode } from '../enums/AccountStatusCode';

/*  
*/
export class SysAdminAccountsFindRequest {
 
    // Filter by name or partial name 
    public search: string;
 
    // Filter by type(s) (empty or UNDEFINED means no filter) 
    public type: AccountTypeCode[];
 
    // Filter by status(s) (empty or UNDEFINED means no filter) 
    public status: AccountStatusCode[];
 
    // Sort by field and direction: (e.g. time+ = sort by time asc, id- = sort by id desc) 
    public sort: string;
 
    // Page number (for pagination) 
    public page: number;
 
    // Number of items per page (for pagination) 
    public pageSize: number;
 
    constructor(search?: string, type?: AccountTypeCode[], status?: AccountStatusCode[], sort?: string, page?: number, pageSize?: number) { 
        this.search = search;
        this.type = type;
        this.status = status;
        this.sort = sort;
        this.page = page;
        this.pageSize = pageSize;
    }

}


