/* 
   General Resource Type (Attributes bit mask) 
*/
export enum ResourceTypeMask {
 
    // Undefined [0] 
    UNDEFINED = 0,
 
    // Single [1] 
    P1 = 1,
 
    // Double [2] 
    P2 = 2,
 
    // Quad [4] 
    P4 = 4,
 
    // Eight [8] 
    P8 = 8,
 
    // Wide [16] 
    WIDE = 16,
 
    // Sculling (2 oars) [32] 
    SCULL = 32,
 
    // Need Cox [64] 
    COX = 64,
 
    // Coastal (use in sea) [128] 
    COASTAL = 128,
 
    // For competition [254] 
    COMP = 254,
 
    // For para-olympic [512] 
    PARA = 512,
 
    // Rowing Boat [1024] 
    RBOAT = 1024,
 
    // Kayak [2048] 
    KAYAK = 2048,
 
    // Surf Ski [4096] 
    SURFSKI = 4096,
 
    // Waves Kayak [8192] 
    WAVES = 8192,
 
}