
/*  
*/
export class UserBookingFindRequest {
 
    // Filter by user who placed the booking 
    public userId: string;
 
    // Filter by resource Id 
    public resourceId: string;
 
    // From booking time [Epoch milliseconds Timestamp] 
    public from: number;
 
    // To booking time [Epoch milliseconds Timestamp] 
    public to: number;
 
    // Sort by field and direction: (e.g. time+ = sort by time asc, id- = sort by id desc) 
    public sort: string;
 
    // Page number (for pagination) 
    public page: number;
 
    // Number of items per page (for pagination) 
    public pageSize: number;
 
    constructor(userId?: string, resourceId?: string, from?: number, to?: number, sort?: string, page?: number, pageSize?: number) { 
        this.userId = userId;
        this.resourceId = resourceId;
        this.from = from;
        this.to = to;
        this.sort = sort;
        this.page = page;
        this.pageSize = pageSize;
    }

}


