import { StringKeyValue } from '../common/StringKeyValue';
import { BaseEntity } from '../entities/BaseEntity';

/* 
   API Key is used per application (e.g. Portal, Mobile App) or service to identify the consumer.
   The access to sets of REST endpoints is restricted according the API key.
   API key also dictates the default session TTL per application (e.g. 20 minutes for Portal or Console, 30 days for Mobile app)
   The application/system name is the Entity Id, the API key itself is not stored in the DB but generated on the fly. 
*/
export class ApiKey extends BaseEntity {
 
    // The API Key (this value is auto generated and not stored in the DB) 
    public key: string;
 
    // Default session token TTL (in minutes), 0 means no TTL 
    public ttl: number;
 
    // Access Control List key value pair - the key is the REST resource name, the value is subset of CRUD (Create | Read |Update |Delete) 
    public acl: StringKeyValue[];
 
    // Key version (for key rotation) 
    public version: number;
 
    // Flag to indicate if this a system key or user defined key 
    public isSystemKey: boolean;
 
    // Flag to indicate if the key is enabled 
    public isEnabled: boolean;
 
    // Entity Id 
    public id: string;
 
    // Entity document type (system field) 
    public _type: string;
 
    // When the entity was created [Epoch milliseconds Timestamp] 
    public createdOn: number;
 
    // When the entity was updated [Epoch milliseconds Timestamp] 
    public updatedOn: number;
 

}


